/**
 * Error type definitions for PostHog tracking
 *
 * These constants categorize errors for filtering and analysis in PostHog.
 */

// Authentication errors
export const AUTH_DEVICE_CODE_INITIATION_FAILED = "auth_device_code_initiation_failed";
export const AUTH_DEVICE_CODE_POLLING_FAILED = "auth_device_code_polling_failed";
export const AUTH_TOKEN_REFRESH_FAILED = "auth_token_refresh_failed";
export const AUTH_SESSION_LOAD_FAILED = "auth_session_load_failed";
export const AUTH_SESSION_SAVE_FAILED = "auth_session_save_failed";

// Sync errors
export const SYNC_NOT_AUTHENTICATED = "sync_not_authenticated";
export const SYNC_EVENTS_UPLOAD_FAILED = "sync_events_upload_failed";
export const SYNC_EVENTS_RETRY_EXHAUSTED = "sync_events_upload_retry_exhausted";
export const SYNC_CHAT_UPLOAD_FAILED = "sync_chat_upload_failed";
export const SYNC_NETWORK_ERROR = "sync_network_error";

// File system errors
export const QUEUE_READ_CORRUPTED = "queue_read_corrupted";
export const QUEUE_WRITE_FAILED = "queue_write_failed";
export const FILE_LOCK_TIMEOUT = "file_lock_timeout";
export const FILE_LOCK_CREATE_FAILED = "file_lock_create_failed";
export const NOTIFICATION_STATE_WRITE_FAILED = "notification_state_write_failed";

// Daemon errors
export const DAEMON_START_FAILED = "daemon_start_failed";
export const DAEMON_RESTART_FAILED = "daemon_restart_failed";
export const DAEMON_SYNC_CYCLE_FAILED = "daemon_sync_cycle_failed";

// API errors
export const API_WORKSPACE_FETCH_FAILED = "api_workspace_fetch_failed";
export const API_PROFILE_UPDATE_FAILED = "api_profile_update_failed";
export const API_STANDUP_TEAM_FETCH_FAILED = "api_standup_team_fetch_failed";
export const API_STANDUP_PROMPT_FETCH_FAILED = "api_standup_prompt_fetch_failed";
export const API_STANDUP_GENERATION_FAILED = "api_standup_generation_failed";

// Supabase client errors
export const SUPABASE_CLIENT_INIT_FAILED = "supabase_client_init_failed";
export const SUPABASE_SESSION_SET_FAILED = "supabase_session_set_failed";
export const SUPABASE_SESSION_REFRESH_PERSIST_FAILED = "supabase_session_refresh_persist_failed";

/**
 * Union type of all error types for type safety
 */
export type ErrorType =
  | typeof AUTH_DEVICE_CODE_INITIATION_FAILED
  | typeof AUTH_DEVICE_CODE_POLLING_FAILED
  | typeof AUTH_TOKEN_REFRESH_FAILED
  | typeof AUTH_SESSION_LOAD_FAILED
  | typeof AUTH_SESSION_SAVE_FAILED
  | typeof SYNC_NOT_AUTHENTICATED
  | typeof SYNC_EVENTS_UPLOAD_FAILED
  | typeof SYNC_EVENTS_RETRY_EXHAUSTED
  | typeof SYNC_CHAT_UPLOAD_FAILED
  | typeof SYNC_NETWORK_ERROR
  | typeof QUEUE_READ_CORRUPTED
  | typeof QUEUE_WRITE_FAILED
  | typeof FILE_LOCK_TIMEOUT
  | typeof FILE_LOCK_CREATE_FAILED
  | typeof NOTIFICATION_STATE_WRITE_FAILED
  | typeof DAEMON_START_FAILED
  | typeof DAEMON_RESTART_FAILED
  | typeof DAEMON_SYNC_CYCLE_FAILED
  | typeof API_WORKSPACE_FETCH_FAILED
  | typeof API_PROFILE_UPDATE_FAILED
  | typeof API_STANDUP_TEAM_FETCH_FAILED
  | typeof API_STANDUP_PROMPT_FETCH_FAILED
  | typeof API_STANDUP_GENERATION_FAILED
  | typeof SUPABASE_CLIENT_INIT_FAILED
  | typeof SUPABASE_SESSION_SET_FAILED
  | typeof SUPABASE_SESSION_REFRESH_PERSIST_FAILED;

/**
 * Error categories for grouping errors in PostHog
 */
export type ErrorCategory = "auth" | "sync" | "filesystem" | "daemon" | "api" | "supabase";

/**
 * Derive error category from error type prefix
 */
export function getErrorCategory(errorType: ErrorType): ErrorCategory {
  if (errorType.startsWith("auth_")) return "auth";
  if (errorType.startsWith("sync_")) return "sync";
  if (
    errorType.startsWith("queue_") ||
    errorType.startsWith("file_") ||
    errorType.startsWith("notification_")
  )
    return "filesystem";
  if (errorType.startsWith("daemon_")) return "daemon";
  if (errorType.startsWith("api_")) return "api";
  if (errorType.startsWith("supabase_")) return "supabase";
  return "api"; // fallback
}
